.row {
  margin-top: 8rem;
  max-width: 114rem;
  margin: 0 auto;
  /* 
    [class*="col-"]{} select all classes that contain col-
    [class^="col-"]{} select all classes that starts col-
    [class$="col-"]{} select all classes that ends col-
    */ }
  .row:not(:last-child) {
    margin-bottom: 8rem; }
    @media (max-width: 56.25em) {
      .row:not(:last-child) {
        margin-bottom: 6rem; } }
  @media (max-width: 56.25em) {
    .row {
      max-width: 50rem;
      padding: 0 3rem; } }
  .row:after {
    content: "";
    display: table;
    clear: both; }
  .row [class^="col-"] {
    float: left; }
    .row [class^="col-"]:not(:last-child) {
      margin-right: 6rem; }
      @media (max-width: 56.25em) {
        .row [class^="col-"]:not(:last-child) {
          margin-right: 0;
          margin-bottom: 6rem; } }
    @media (max-width: 56.25em) {
      .row [class^="col-"] {
        width: 100% !important; } }
  .row .col-2 {
    width: calc((100% - 6rem) / 2); }
  .row .col-3 {
    width: calc((100% - (12rem)) / 3); }
  .row .col-4 {
    width: calc((100% - (18rem)) / 4); }
  .row .col-1-3 {
    width: calc( 2 * ((100% - (12rem)) / 3) + 6rem); }

