.row {
  margin-top: $gutter-vertical;
  max-width: $grid-width;
  margin: 0 auto;
  &:not(:last-child) {
    margin-bottom: $gutter-vertical;
    @include responsive(tab-port) {
      margin-bottom: $gutter-vertical-small;
    }
  }

  @include responsive(tab-port) {
    max-width: 50rem;
    padding: 0 3rem;
  }

  @include clearFix;
  /* 
    [class*="col-"]{} select all classes that contain col-
    [class^="col-"]{} select all classes that starts col-
    [class$="col-"]{} select all classes that ends col-
    */
  [class^="col-"] {
    float: left;
    &:not(:last-child) {
      margin-right: $gutter-horizontal;
      @include responsive(tab-port) {
        margin-right: 0;
        margin-bottom: $gutter-vertical-small;
      }
    }

    @include responsive(tab-port) {
      width: 100% !important;
    }
  }
  .col-2 {
    width: calc((100% - #{$gutter-horizontal}) / 2);
  }
  .col-3 {
    width: calc((100% - (#{$gutter-horizontal * 2})) / 3);
  }
  .col-4 {
    width: calc((100% - (#{$gutter-horizontal * 3})) / 4);
  }
  .col-1-3 {
    width: calc(
      2 * ((100% - (#{$gutter-horizontal * 2})) / 3) + #{$gutter-horizontal}
    );
  }
}
