/* Animations */
@keyframes moveInLeft {
  /* Browsers are more optimised for opacity and transform properties*/
  0% {
    opacity: 0;
    /* shift the element -100px from its position towards the right*/
    transform: translateX(-10rem);
  }
  80% {
    opacity: 0.8;
    /* go 20px beyond the element position */
    transform: translateX(1rem);
  }
  100% {
    opacity: 1;
    /* get back to the original position */
    transform: translate(0);
  }
}
@keyframes moveInRight {
  /* Browsers are more optimised for opacity and transform properties*/
  0% {
    opacity: 0;
    /* shift the element 100px from its position towards the left*/
    transform: translateX(10rem);
  }
  80% {
    opacity: 0.8;
    /* go 10px beyond the element position */
    transform: translateX(-1rem);
  }
  100% {
    opacity: 1;
    /* get back to the original position */
    transform: translate(0);
  }
}
@keyframes moveInBottom {
  /* Browsers are more optimised for opacity and transform properties*/
  0% {
    opacity: 0;
    /* shift the element -100px from its position towards the right*/
    transform: translateY(10rem);
  }
  80% {
    opacity: 0.8;
    /* go 20px beyond the element position */
    transform: translateY(-1rem);
  }
  100% {
    opacity: 1;
    /* get back to the original position */
    transform: translate(0);
  }
}
